main {
	margin: 0 auto;
	max-width: 90%;
  padding: 50px 30px;
}
:root {
  --primary: #8c54ff;
  --secondary: #F3A847;
  --tertiary: #B12704;
  --light: #fafafa;
  --dark: #000000;
  --darker: #111111;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
  --spacing-five: calc(2.5rem);

}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-weight: 600;
  line-height: 1.5;
  font-size: 18px;
  letter-spacing: .1em;
  background: var(--dark);
  color: var(--light);
  /* font-family: 'Poiret One', cursive; */

}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

a {
  color: var(--tertiary);
  text-decoration: none;
  font-weight: bold;
}

a:hover {
  text-decoration: underline;
}

header {
  background: var(--dark);
}

header a {
  color: var(--light);
}

button {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  border-radius: .5rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 1px;
  background-color: var(--secondary);
}

button:hover {
  opacity: .8;
}

button[disabled] {
  cursor: default;
  opacity: .8;
}

img {
  max-width: 100%;
}

span[role="img"] {
  cursor: default;
}

form {
  width: 40%;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

li {
  list-style: none;
}

nav a:hover {
  text-decoration: none;
}

nav  li {
  cursor: pointer;
}

textarea {
	width: 600px;
	height: 120px;
	border: 3px solid #cccccc;
	padding: 5px;
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: var(--spacing-one);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: var(--spacing-two);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: var(--spacing-two);
  margin-bottom: var(--spacing-two);
}

.my-5 {
  margin-top: var(--spacing-five);
  margin-bottom: var(--spacing-five);
}

.px-1 {
  padding-right: var(--spacing-one);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: var(--spacing-two);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: var(--spacing-one);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: var(--spacing-two);
  padding-bottom: var(--spacing-two);
}

.card {
  width: 25%;
  text-align: center;
}

.img-thumbnail {
  width: auto;
  height: 300px;
  margin-bottom: 50px;
  cursor: pointer;
  position: relative;
  display: block;
}

.modalBackdrop {
	background: rgba(0, 0, 0, .65);
	bottom: 0;
	left: 0;
	overflow: auto;
	position: fixed;
	right: 0;
	top: 0;
	z-index: 1;
}

.modalContainer {
	background: var(--darker);
	border-radius: 5px;
	max-width: 100%;
	margin: 50px auto;
	padding: 15px;
	width: 960px;
}

.modalTitle {
	font-size: 18px;
	font-weight: 600;
	margin-bottom: 25px;
}

button {
	background: var(--primary);
	border: 0;
	border-radius: 5px;
	color: #fff;
	cursor: pointer;
	font-size: 16px;
	font-weight: 600;
	margin-bottom: 30px;
	padding: 10px 15px;	
}

a {
	border-radius: 5px;
	color: var(--primary);
	font-weight: 600;
	padding: 2px 4px;
	margin-left: -2px;
	margin-right: -2px;
	
}

.navActive {
  color: var(--secondary);
}

footer {
  text-align: center;
  display: inline;
  position: absolute;
  bottom: 20;
  width: 100%;
  height: 2.5rem; 
  margin: 350px 1rem;
}

.portfolio-image {
  margin: 3%;
  position: relative;
  height: 300px;
  width: 520px;
  display: block;
  /* ////inline-block */
}

.fa-linkedin {
  font-size: 45px;
  position: relative;
}

.fa-github {
  font-size: 45px;
  position: relative; 
  bottom: 10px; 
  left: 0;
} 

.fa-github-portfolio {
  font-size: 45px;
  position: relative;
}

.fa-github-contact-page {
  font-size: 50px;
}

.portfolio-image-application-url{
  position: absolute;
  top: 30px;
  left: 50px;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  background-color: black;
  color: white;
  display: block;
}

.portfolio-image-application-repo{
  position: absolute;
  top: 35px;
  right: 50px;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-weight: bold;
  background-color: black;
  color: white;
  display: block;
}

#resume-link {
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.footer-link {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.nav-links {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.about-me-p-tag {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.contact-information {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 40px;
  padding-top: 40px;
}

.contact-form-label {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.error-text {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  color: red;
}

.about { max-width: 850px; margin: 0 auto; }

.headshot {
  float: left;             
  width: 220px;          
  height: auto;             
  margin: 0 16px 12px 0;    
  border-radius: 12px;      
  object-fit: cover;
  display: block;
}

.about .about-me-p-tag { line-height: 1.6; }

.contact-list {
  list-style: none;
  margin: 30px 0 0;
  padding: 0;
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr)); /* wider cards */
  align-items: stretch;
}
.contact-list > li { margin: 0; }

.contact-card {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 22px 24px;   
  min-height: 200px;    
  width: 100%;
  box-sizing: border-box;

  background: #121214;  
  color: #e9e9ef;
  text-decoration: none;
  border: 1px solid rgba(255,255,255,.10);
  border-radius: 14px;
  box-shadow: 0 2px 12px rgba(0,0,0,.35), inset 0 1px 0 rgba(255,255,255,.03);

  transition: transform .12s ease, border-color .12s ease, background .12s ease;
}
.contact-card:hover {
  background: #15171a;
  border-color: rgba(255,255,255,.2);
  transform: translateY(-2px);
}
.contact-card:focus-visible {
  outline: 2px solid rgba(139,92,246,.5);
  outline-offset: 2px;
}

.contact-icon {
  display: grid;
  place-items: center;
  width: 64px;      
  height: 64px;
  border-radius: 14px;
  background: rgba(139,92,246,.18);
  color: #b79bff;        
  flex: 0 0 auto;
}
.contact-icon svg {
  width: 50px;        
  height: 50px;
}

.contact-title {
  font-size: 1.15rem;
  font-weight: 600;
  line-height: 1.25;
}

.skills {
  display: grid;
  gap: 24px;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  align-items: start;
}

@media (max-width: 680px) {
  .skills {
    grid-template-columns: 1fr;
  }
}

.skills-list {
  list-style: none;
  margin: 12px 0 0;
  padding: 0;

  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 8px 12px;
}

.li-proficiencies {
  padding: 6px 10px;
  border-radius: 8px;
  background: #121214; 
  border: 1px solid rgba(255,255,255,.08);
  color: #e9e9ef;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 15px;
  text-align: center;
}

.resume-link {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  text-decoration: none;
}

.resume-button {
  font-size: 1.1rem;
  padding: 12px 28px;
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.15);
  background-color: #8b5cf6;
  color: white;
  cursor: pointer;
  transition: background-color 0.2s ease, transform 0.15s ease;
}

.resume-button:hover {
  background-color: #7c3aed;
  transform: translateY(-2px);
}

.resume-button:active {
  transform: translateY(0);
}


@media screen and (max-width: 900px) {
  .fa-github {
    font-size: 25px;
    position: relative; 
    bottom: 10px; 
    left: 0;
    padding-top: 40px;
  } 

  .img-thumbnail {
    padding-bottom: 50px;
  }
}

@media screen and (max-width: 1300px) {
  .fa-github {
    font-size: 35px;
    position: relative; 
    bottom: 10px; 
    left: 0;
    padding-top: 40px;
  } 

}



